@import url('https://fonts.googleapis.com/css2?family=Outfit:wght@300;400;600&display=swap');



:root {
    
    font-size: 15px;
    --var-soft-blue: hsl(215, 51%, 70%);
    --var-cyan: hsl(178, 100%, 50%);
    --var-main-darkest: hsl(217, 54%, 11%);
    --var-card-dark: hsl(216, 50%, 16%);
    --var-line-dark: hsl(215, 32%, 27%);
    --var-lightest: white;
    
    --var-heading: normal normal 600 1.5em/1.6em 'Outfit', sans-serif;
    
    --var-small-heading: normal normal 400 1em/1em 'Outfit', sans-serif;
    
    --var-para: normal normal 300 1em/1.55em 'Outfit', sans-serif;
}

html {
    box-sizing: border-box;
}

*, *::before, *::after {
    box-sizing: inherit;
    margin: 0;
}

.cardBody {
    display: inline-block;
}

img {
    width: 100%;
    border-radius: 15px;
    display: block;
}

.hero-image{
  height: 150px;
  object-fit: cover;
}

a {
    color: inherit;
}

h1 {
    font-size: 20px;
    color: var(--var-lightest);
    padding: 6px 0;
    word-break: break-all;
}

h2 {
  font-size: 20px;
  margin-right: 4px;
    color: var(--var-lightest);
    /* padding on .coin-base */
}

.langName{
  font-size: 16px;
  text-transform: uppercase;
}

p {
    font: var(--var-para);
    color: var(--var-soft-blue);
    /* word-break: break-all; */
}

span {
    color: white;
}

.card-container {
    width: 200px;
    /* margin: 20px 0 0 20px; */
    background-color: var(--var-card-dark);
    border-radius: 15px;
    padding: 10px;
    height: 100%;
    overflow: hidden;
}

div.flex-row {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
}

div.coin-base, .time-left, .card-attribute {
    display: flex;
    align-items: center;
    padding: 1em 0;
}

.card-attribute {
    padding-bottom: 1.5em;
    border-top: 2px solid var(--var-line-dark);
}

a.hero-image-container {
    position: relative;
    display: block;
}



/* Details */

img.eye {
    position: absolute;
    width: 100%;
    max-width: 2em;
    top: 44%;
    left: 43%;
}

@media (min-width:400px) {
  img.eye {
    max-width: 3em;
}
}

.hero-image-container::after {
    content: '';
    background-image: url("https://i.postimg.cc/9MtT4GZY/view.png");
    background-position: center;
    background-repeat: no-repeat;
    background-size: 5rem;
    background-color: hsla(178, 100%, 50%, 0.3);
    width: 100%;
    height: 100%;
    border-radius: 1rem;
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    z-index: 2;
    opacity: 0;
    transition: opacity 0.3s ease-out;
}


.small-image {
    width: 1.2em;
    margin-right: .5em;
}

.small-avatar {
    width: 2em;
    border-radius: 200px;
    outline: 2px solid white;
    margin-right: 1.4em;
}

div.attribution {
    margin: 0 auto;
    width: 100%;
    font: var(--var-para);
    text-align: center;
    padding: 1.5em 0 4em 0;
    color: var(--var-line-dark);
}
.attribution a {
    color: var(--var-soft-blue);
}

@media (min-width:600px) {
    .cardBody {
        /* font-size: 18px; */
    }
}

@media (max-width:768px) {
    .card-container{
        width: 170px;
    }
}